import React, { useEffect, useState } from 'react'
import { Button, Form, } from 'react-bootstrap'
import { AppConfig, UserSession, openSignatureRequestPopup, showConnect } from '@stacks/connect'
import { verifyMessageSignatureRsv } from '@stacks/encryption'
import { StacksMainnet, StacksTestnet } from '@stacks/network'
// import { createTransactionWithMessage } from '../core/core.js'

const IS_TESTNET = true

const USER_DISCONNECTED = 1
const USER_CONNECTING = 2
const USER_CONNECTED = 3

export const HiroProvider = (props) => {
	const [isWalletInstalled, setWalletInstalled] = useState(false)
	const [userState, setUserState] = useState(USER_DISCONNECTED)
	const [userData, setUserData] = useState(null)
	const [message, setMessage] = useState('')
	const [publicKey, setPublicKey] = useState('')
	const [signature, setSignature] = useState('')

	// console.

	window.onload = () => {
		setWalletInstalled(window.StacksProvider)

		if (userSession.isUserSignedIn()) {
			setUserData(userSession.loadUserData())
			setUserState(USER_CONNECTED)
		} else if (userSession.isSignInPending()) {
			setUserState(USER_CONNECTING)
			userSession.handlePendingSignIn()
				.then(userData => {
					setUserData(userData)
					setUserState(USER_CONNECTED)
				})
				.catch(error => {
					console.log(error)
					alert(error.message)

					setUserState(USER_DISCONNECTED)
				})
		}
	}

	const appConfig = new AppConfig()
	const userSession = new UserSession({ appConfig })

	const connectWallet = () => {
		try {
			setUserState(USER_CONNECTING)

			showConnect({
				userSession,
				network: IS_TESTNET ? StacksTestnet : StacksMainnet,
				appDetails: {
					name: props.appName,
					icon: window.location.origin + '/favicon.ico',
				},
				onFinish: () => {
					setUserData(userSession.loadUserData())
					setUserState(USER_CONNECTED)
				},
				onCancel: () => {
					setUserState(USER_DISCONNECTED)
				},
			})
		} catch (error) {
			console.error(error)
			alert(error.message)

			setUserState(USER_DISCONNECTED)
		}
	}

	const disconnectWallet = async () => {
		userSession.signUserOut()
		setUserData(null)
		setUserState(USER_DISCONNECTED)
	}

	const signMessage = async () => {
		try {
			// const unsignedTransaction = createTransactionWithMessage(message)

			if (message) {
				await openSignatureRequestPopup({
					message: message,
					network: IS_TESTNET ? StacksTestnet : StacksMainnet,
					appDetails: {
						name: props.appName,
						icon: window.location.origin + '/favicon.ico',
					},
					onFinish: ({ publicKey, signature }) => {
						setSignature(signature)
						setPublicKey(publicKey)

						const verified = verifyMessageSignatureRsv({ signature, message, publicKey })
						if (verified) {
							alert('Verify message signature success')
						} else {
							alert('Verify message signature fail')
						}
					},
					onCancel: () => { }
				})
			}
		} catch (error) {
			console.error(error)
			alert(error.message)
		}
	}

	return (
		<div>
			{!isWalletInstalled && (
				<>
					<h4 className='my-5'>
						Hiro wallet is not installed.
					</h4>
				</>
			)}
			{isWalletInstalled &&
				<>
					{userState === USER_DISCONNECTED && (
						<>
							<Button onClick={connectWallet}> Connect to Hiro </Button>
						</>
					)}
					{userState === USER_CONNECTING && (
						<>
							<Button disabled={true}> Connecting </Button>
						</>
					)}
					{userState === USER_CONNECTED && (
						<>
							<Button onClick={disconnectWallet}> Disconnect from Hiro </Button>
							<p> P2WPKH address : {(userData.profile.btcAddress.p2wpkh[IS_TESTNET ? 'testnet' : 'mainnet']).slice(0, 4)}...{(userData.profile.btcAddress.p2wpkh[IS_TESTNET ? 'testnet' : 'mainnet']).slice(-4)} </p>
							<p> P2TR address : {(userData.profile.btcAddress.p2tr[IS_TESTNET ? 'testnet' : 'mainnet']).slice(0, 4)}...{(userData.profile.btcAddress.p2tr[IS_TESTNET ? 'testnet' : 'mainnet']).slice(-4)} </p>
							<Form>
								<Form.Group>
									<Form.Label>Message</Form.Label>
									<Form.Control column placeholder='Input message' value={message} onChange={(event) => setMessage(event.target.value)} />
									<Button onClick={signMessage}>Sign</Button>
								</Form.Group>
								<Form.Group>
									<Form.Label>Public key: </Form.Label>
									<Form.Control readOnly={true} value={publicKey} />
								</Form.Group>
								<Form.Group>
									<Form.Label>Signature: </Form.Label>
									<Form.Control readOnly={true} value={signature} />
								</Form.Group>
							</Form>
						</>
					)}
				</>
			}
		</div>
	)
}